"use scrict";
import Exhibition from "@/components/Exhibition/index.vue";
import Not from "@/components/Not/index.vue";
import SendItemUi from "@/components/SendItemUi/index.vue";
import ReceiveSendFooter from "@/components/ReceiveSendFooter/index.vue";
import { COLOR_PRIMARY, COLOR_TEXT } from "@/config/const.js";
import { NOTICE_DETAIL, SEND_NOTICE } from "@/config/router.js";

export default {
	data() {
		return {
			menus: [
				{label: "已收通知", icon: "iconkongneirong"},
				{label: "已发通知", icon: "iconfasong1"}
			],
			index: 0,
			noticeList: [],
			sendList: [],
			COLOR_TEXT,
			COLOR_PRIMARY
		}
	},
	onLoad(options) {
		this.$eventBus.$on("setNoticeIndex", value => {
			this.index = value;
		});
		this.$eventBus.$on("updateNoticeList", value => {
			this.loadList(value);
		});
		this.loadList(2);
	},
	watch: {
		index(value) {
			value === 1 && !this.sendList.length && this.loadList(1);
		}
	},
	methods: {
		loadList: function (type) {
			this.$post(this.$api.GET_NOTICE_LIST, {type, status: 1}).then(res => {
				switch(type) {
					case 1:
						this.sendList = res.data.list;
						break;
					case 2:
						this.noticeList = res.data.list;
						break;
				}
			});
		},
		linkToNoticeDetail: function (item) {
			this.$toPage(NOTICE_DETAIL, { id: item.id, idType: this.index ? 1 : 2 });
		},
		linkToSendNotice: function () {
			this.$toPage(SEND_NOTICE);
		}
	},
	components: {
		Exhibition,
		Not,
		SendItemUi,
		ReceiveSendFooter
	}
}
